import React from 'react';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import VSpace from './../util/vSpace';
import TopBar from './topBar';
import NameBox from './../welcome/nameBox';
import Button from './../util/button';
import About from './../about/about'

import './../util/title.css';
import './../util/body.css';

const prideColors = [ {backgroundColor: "rgba(234, 62, 55, 0.9)"},
                      {backgroundColor: "rgba(246, 143, 31, 0.9)"},
                      {backgroundColor: "rgba(246, 213, 9, 0.9)"},
                      {backgroundColor: "rgba(168, 203, 56, 0.9)"},
                      {backgroundColor: "rgba(32, 154, 207, 0.9)"},
                      {backgroundColor: "rgba(170, 82, 160, 0.9)"},
                     ];

const interval = clearInterval();

//The entire application will live inside of the home component!
class Home extends React.Component {
  constructor(props) {
    super(props);

    document.body.style.overflow = "hidden";
    this.state = {
      colorIndex: 0,
      currentColor: {},
      updateColor: true,
      topBarText: "",
      menuItems: [],
      hasRenderedTopBar: false,
      displayingTopScreen: true,
      currentScreen: "HOME", //Can be "HOME", "PROJECTS", "ABOUT", etc.
      topPage: <div>This is the top page </div>,
      bottomPage: <div>This is the bottom page </div>,
      width: window.innerWidth,
      height: window.innerHeight,
      heightOfCurrentPage: 0,
    };
    this.updateColor = this.updateColor.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.mouseOverTopBar = this.mouseOverTopBar.bind(this);
    this.mouseNotOnTopBar = this.mouseNotOnTopBar.bind(this);
    this.switchScreen = this.switchScreen.bind(this);
    this.sleep = this.sleep.bind(this);
    this.renderPage = this.renderPage.bind(this);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }

  scrollToTop() {
    scroll.scrollToTop();
  }

  componentDidMount() {
    //Listening for the scroll to determine if the topbar should be in view or not.
    window.addEventListener('scroll', this.handleScroll);

    //The interval for updating the color of the banner with the name and the topbar.
    this.interval = setInterval(() => {
      if(this.state.updateColor){
        this.setState({colorIndex: (this.state.colorIndex + 1) % prideColors.length});
      }
      this.updateColor();},
      1000);

      //Listener for update of windowdimension
      this.updateWindowDimensions();
      window.addEventListener('resize', this.updateWindowDimensions);
  }

  componentWillUnmount() {
    console.log("unmountes");
    window.removeEventListener('scroll', this.handleScroll);
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
    console.log(this.state.height);
  }

  handleScroll(event) {
    if(window.pageYOffset > 167){

      if(!this.state.hasRenderedTopBar){
        this.setState({updateColor: false});
        this.setState({topBarText: "CREMIS"});
        this.setState({hasRenderedTopBar: true});
      }
    } else {
      this.setState({updateColor: true});
      this.setState({topBarText: ""});
      this.setState({menuItems: []});
      this.setState({hasRenderedTopBar: false});

    }
  }

  updateColor() {
    this.setState({currentColor: prideColors[this.state.colorIndex]});
  }

  mouseOverTopBar() {
    this.setState({updateColor: true});
    this.setState({menuItems: ["HOME", "PROJECTS", "ABOUT", "OTHER..."]});
  }

  mouseNotOnTopBar() {
  this.setState({updateColor: false});
  this.setState({menuItems: []});

  }

  sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  //scroller.scrollTo('topPage', {
  //  duration: 800,
  //  delay: 0,
  //  smooth: 'easeInOutQuart'
  //});

  renderPage(newScreen) {
    let myDiv = <div></div>
      switch (newScreen) {
        case "ABOUT":
        myDiv = <About />
        return myDiv;
        break;

        case "PROJECTS":
        myDiv = <div>Welcome to my projects. There are no projekts here at the moment...</div>;
        return myDiv;
        break;

        case "OTHER...":
        myDiv = <div>This is suppose to be the OTHER... part of the page... Tja, tja OTHER...gen! osv</div>;
        return myDiv;
      }
  }

  renderScreen(newScreen, location) {
    switch (location) {
      case "TOP":
        let newTopPage = this.renderPage(newScreen);
        this.setState({topPage: newTopPage})
        break;

      case "BOTTOM":
        let newBottomPage = this.renderPage(newScreen);
        this.setState({bottomPage: newBottomPage})
        break;

      default:

    }
  }

  switchScreen(newScreen) {

    if(newScreen === this.state.currentScreen){
      //Do nothhing
    } else if(this.state.displayingTopScreen){
      this.setState({bottomPage: newScreen});
      this.setState({currentScreen: newScreen})
      this.renderScreen(newScreen, "BOTTOM");
      this.setState({displayingTopScreen: false})
      scroller.scrollTo('bottomPage', {
        duration: 600,
        delay: 0,
        smooth: 'easeInOutQuart',
        offset: -75,
      });
    } else {
      this.setState({currentTopPage: newScreen});
      this.setState({displayingTopScreen: true});
      this.setState({currentScreen: newScreen})
      this.renderScreen(newScreen, "TOP");
      scroller.scrollTo('topPage', {
        duration: 600,
        delay: 0,
        smooth: 'easeInOutQuart',
        offset: -75,
      });
    }

  }


  render() {
    return(
      <body id="topOfBody">


        <div onMouseOver={this.mouseOverTopBar} onMouseLeave={this.mouseNotOnTopBar} >
        <TopBar currentColor={this.state.currentColor} text={this.state.topBarText} onClick={scroll.scrollToTop}/>
        <div class="menuItem" style={{left: "70px"}} onClick={scroll.scrollToTop}>{this.state.menuItems[0]}</div>
        <div class="menuItem" style={{left: "150px"}} newScreen={"PROJECTS"} onClick={() => this.switchScreen("PROJECTS")} >{this.state.menuItems[1]}</div>
        <div class="menuItem" style={{left: "230px"}} newScreen={"ABOUT"} onClick={() => this.switchScreen("ABOUT")}>{this.state.menuItems[2]}</div>
        <div class="menuItem" style={{left: "300px"}} newScreen={"OTHER..."} onClick={() => this.switchScreen("OTHER...")}>{this.state.menuItems[3]}</div>
        </div>






        <div>
        <NameBox currentColor={this.state.currentColor}/>
        <div class="title">
        Welcome to my personal page!
        <VSpace padding={this.state.height / 4}/>
        </div>
        <div>
        <div>
        <Link activeClass="active"
          className="test1"
          to="topPage"
          spy={false}
          smooth={true}
          duration={600}
          offset={-75}
        >
        <Button text="ENTER" onClick={() => this.switchScreen("ABOUT")}/>
        </Link>
        </div>
        </div>


          <div class="title">
          <VSpace padding={600} />
          <Element name="topPage" className="element">
          Navigation
          </Element>
          </div>
          <div>
            <VSpace padding={30} />
          </div>
          <div class="body">
            {this.state.topPage}
          </div>
          </div>

          <div class="title">
          <VSpace padding={800} />
          <Element name="bottomPage" className="element">
          Navigation
          </Element>
          </div>
          <div>
            <VSpace padding={30} />
          </div>
          <div class="body">
            {this.state.bottomPage}
            <VSpace padding={800}/>
          </div>


      </body>
    );
  }

}

export default Home;
